<template>
  <div>
    <input type="file" @change="onImageUpload" accept="image/*">
    <input type="text" v-model="overlayText" placeholder="Enter text to overlay">
    <button @click="downloadImage">Download Image</button>
    <canvas ref="canvas" :width="canvasWidth" :height="canvasHeight" @mousedown="startDrag" @mousemove="drag" @mouseup="stopDrag" @mouseleave="stopDrag"></canvas>
  </div>
</template>
<script>
import { ref, watch, onMounted } from 'vue'

export default {
  setup () {
    const overlayText = ref('')
    const image = ref(null)
    const canvasWidth = ref(800)
    const canvasHeight = ref(600)
    const dragging = ref(false)
    const dragStartY = ref(0)
    const textPosition = ref({ x: 50, y: 50 })

    const onImageUpload = (event) => {
      const file = event.target.files[0]
      if (file) {
        const reader = new FileReader()
        reader.onload = (e) => {
          const img = new Image()
          img.onload = () => {
            canvasWidth.value = img.width
            canvasHeight.value = img.height
            image.value = img
            drawCanvas()
          }
          img.src = e.target.result
        }
        reader.readAsDataURL(file)
      }
    }

    const drawCanvas = () => {
      const canvas = document.querySelector('canvas')
      const ctx = canvas.getContext('2d')
      ctx.clearRect(0, 0, canvasWidth.value, canvasHeight.value)
      if (image.value) {
        ctx.drawImage(image.value, 0, 0)
      }
      if (overlayText.value) {
        ctx.font = '48px serif'
        ctx.fillStyle = 'white'
        ctx.fillText(overlayText.value, textPosition.value.x, textPosition.value.y)
      }
    }

    const downloadImage = () => {
      const canvas = document.querySelector('canvas')
      const link = document.createElement('a')
      link.href = canvas.toDataURL('image/png')
      link.download = 'image_with_text.png'
      link.click()
    }

    const startDrag = (event) => {
      dragging.value = true
      dragStartY.value = event.offsetY - textPosition.value.y
    }

    const drag = (event) => {
      if (dragging.value) {
        textPosition.value.y = event.offsetY - dragStartY.value
        drawCanvas()
      }
    }

    const stopDrag = () => {
      dragging.value = false
    }

    watch(overlayText, drawCanvas)

    onMounted(() => {
      drawCanvas()
    })

    return {
      overlayText,
      onImageUpload,
      downloadImage,
      canvasWidth,
      canvasHeight,
      startDrag,
      drag,
      stopDrag
    }
  }
}
</script>

<style scoped>
#canvas {
  border: 1px solid black;
}
</style>
